export default {
  name: "myWishList",

  data() {
    return {
      shopData: [],
      loadShow: false,
      pageData: null,
      pages: 1
    };
  },
  mounted() {
    this.getShopData();
  },
  methods: {
    goPageNum(pageNum) {
      this.pages = pageNum;
      this.getShopData();
    },
    prevPage() {
      let { current_page, total_page } = this.pageData;
      if (current_page > 1) {
        this.pages--;
        this.getShopData();
      }
    },
    nextPage() {
      let { current_page, total_page } = this.pageData;
      if (current_page < total_page) {
        this.pages++;
        this.getShopData();
      }
    },
    getShopData() {
      let thif = this;
      this.loadShow = true;
      let user_id = this.$store.getters.getUserId;
      let obj = {
        per_page: 12,
        page: this.pages
      };
      this.$apis
        .getFavoritesList(obj, user_id)
        .then(res => {
          if (res.status == 1) {
            thif.loadShow = false;
            thif.shopData = res.data.items;
            thif.pageData = res.data.pages;
          } else {
            thif.loadShow = false;
            thif.common.msgBox(thif, res.msg, 3);
          }
        })
        .catch(err => {
          thif.loadShow = false;
          console.log(err);
        });
    }
  }
};
